import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { InternalUserAddEditComponent } from './internal-user-add-edit/internal-user-add-edit.component';
import { InternalUserListComponent } from './internal-user-list/internal-user-list.component';
import { InternalUserDetailComponent } from './internal-user-detail/internal-user-detail.component';

const routes: Routes = [
  {
    path: '',
    component: InternalUserListComponent
  },
  {
    path: 'add-edit',
    component: InternalUserAddEditComponent
  },
  {
    path: 'detail',
    component: InternalUserDetailComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserMgmtRoutingModule { }
